.vrk {
/*animation: VRK 1.5s 1 0s ease-out;*/
}

@-webkit-keyframes VRK {
  0% {
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
  }

  50% {
    -webkit-transform: translateX(100px);
            transform: translateX(100px);
  }

  100% {
    -webkit-transform: rotate(xx) translateX(0px);
            transform: rotate(xx) translateX(0px);
  }
}

@keyframes VRK {
  0% {
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
  }

  50% {
    -webkit-transform: translateX(100px);
            transform: translateX(100px);
  }

  100% {
    -webkit-transform: rotate(xx) translateX(0px);
            transform: rotate(xx) translateX(0px);
  }
}
